"use client";

import Image from "next/image";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLinkedin, faGithub, faTwitter } from "@fortawesome/free-brands-svg-icons";
import { faEnvelope } from "@fortawesome/free-solid-svg-icons";

export default function ContactSection() {
    return (
        <section id="contact" className="min-h-screen flex items-center justify-center bg-white text-black py-20 px-6 relative z-50">
            <div className="max-w-6xl w-full mx-auto grid grid-cols-1 md:grid-cols-2 gap-12 items-center">

                {/* Text Content */}
                <div className="space-y-8 order-2 md:order-1">
                    <div>
                        <h2 className="text-4xl md:text-6xl font-bold tracking-tighter mb-4 text-black">
                            Let's <span className="text-blue-500">Connect</span>
                        </h2>
                        <div className="h-1 w-20 bg-blue-500 rounded-full mb-6"></div>
                        <p className="text-lg text-gray-600 leading-relaxed max-w-lg">
                            I'm always open to discussing new projects, creative ideas, or opportunities to be part of your visions.
                        </p>
                    </div>

                    <div className="space-y-4">
                        <div className="flex items-center space-x-4 group cursor-pointer">
                            <div className="w-12 h-12 bg-gray-200 rounded-full flex items-center justify-center group-hover:bg-blue-600 transition-colors duration-300">
                                <FontAwesomeIcon icon={faEnvelope} className="text-xl text-gray-700 group-hover:text-white" />
                            </div>
                            <span className="text-xl font-medium text-gray-800 group-hover:text-black transition-colors">hello@example.com</span>
                        </div>
                    </div>

                    <div className="flex space-x-4 pt-4">
                        <SocialLink icon={faLinkedin} href="#" />
                        <SocialLink icon={faGithub} href="#" />
                        <SocialLink icon={faTwitter} href="#" />
                    </div>
                </div>

                {/* Photo */}
                <div className="order-1 md:order-2 flex justify-center md:justify-end">
                    <div className="relative w-72 h-72 md:w-96 md:h-96 rounded-full overflow-hidden border-4 border-gray-200 shadow-2xl grayscale hover:grayscale-0 transition-all duration-500">
                        <Image
                            src="/images/linkedinphoto.png"
                            alt="Profile"
                            fill
                            className="object-cover"
                        />
                    </div>
                </div>
            </div>
        </section>
    );
}

function SocialLink({ icon, href }: { icon: any, href: string }) {
    return (
        <a href={href} className="w-12 h-12 rounded-full border border-gray-300 flex items-center justify-center text-gray-500 hover:bg-black hover:text-white hover:border-black transition-all duration-300">
            <FontAwesomeIcon icon={icon} className="text-lg" />
        </a>
    );
}
