// Placeholder data for projects
// ... (existing data)

export default function ProjectsSection() {
    return (
        <section className="min-h-0 bg-[#9a9a98] text-white py-20 px-4 md:px-12 relative z-20">
            <div className="max-w-7xl mx-auto">
                <div className="mb-16">
                    <h2 className="text-4xl md:text-6xl font-bold mb-4 tracking-tighter text-white">
                        Selected <span className="text-white">Works</span>
                    </h2>
                    <div className="h-1 w-20 bg-white rounded-full"></div>
                    <p className="mt-6 text-white/90 max-w-2xl text-lg">
                        A curated collection of failed experiments, successful pivots, and everything in between.
                        Explore the chaos.
                    </p>
                </div>

                {/* Staggered Grid (Masonry) using CSS Columns */}
                <div className="columns-1 md:columns-2 lg:columns-3 gap-8 space-y-8">
                    {projects.map((project) => (
                        <div
                            key={project.id}
                            className="break-inside-avoid relative group rounded-2xl overflow-hidden bg-white shadow-xl hover:shadow-2xl transition-all duration-500 ease-out hover:-translate-y-2"
                        >
                            {/* Image Placeholder Area */}
                            <div className={`w-full ${project.height} ${project.color} relative overflow-hidden`}>
                                <div className="absolute inset-0 bg-black/10 group-hover:bg-transparent transition-colors duration-500" />
                            </div>

                            {/* Content */}
                            <div className="p-6 relative">
                                {/* Floating Icon/Badge equivalent */}
                                <div className="absolute -top-6 right-6 w-12 h-12 bg-gray-900 rounded-full flex items-center justify-center shadow-lg transform translate-y-2 opacity-0 group-hover:opacity-100 group-hover:translate-y-0 transition-all duration-300 delay-100">
                                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" className="w-6 h-6 text-white">
                                        <path strokeLinecap="round" strokeLinejoin="round" d="M4.5 19.5l15-15m0 0H8.25m11.25 0v11.25" />
                                    </svg>
                                </div>

                                <h3 className="text-2xl font-bold mb-2 text-gray-900 group-hover:text-blue-600 transition-colors duration-300">
                                    {project.title}
                                </h3>
                                <p className="text-gray-600 mb-4 text-sm leading-relaxed">
                                    {project.description}
                                </p>

                                <div className="flex flex-wrap gap-2 mt-4">
                                    {project.tags.map(tag => (
                                        <span key={tag} className="px-3 py-1 text-xs font-medium rounded-full bg-gray-100 text-gray-700 border border-gray-200">
                                            {tag}
                                        </span>
                                    ))}
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}
